import React, { Component } from 'react'
import cryptoplatimg1 from "./assets/img/platfrom-1.png";
import cryptoplatimg2 from "./assets/img/platfrom-2.png";
import cryptoplatimg3 from "./assets/img/platfrom-3.png";
import mayaimg from "./assets/img/maya.png";
import aeroplaneimg from "./assets/img/arrow-plane.png";
import processimg1 from "./assets/img/process-1.png";
import processimg2 from "./assets/img/process-2.png";
import processimg3 from "./assets/img/process-3.png";

export class Cryptocurrency extends Component {
    render() {
        return (
            <div className="platfromland">
                <div className="platform" id="platform">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 text-center">
                                <div className="section-head">
                                    <h4 className="lasthead">The Most Trusted</h4>
                                    <h2 className="title">Cryptocurrency Platform</h2>
                                    <p className="text">
                                        Our priority is to provide quick, secure and the best services to our customers.
                                    </p>
                                </div>
                            </div>
                            <div className="col-xl-4 col-lg-6 text-center">
                                <div className="platform-box">
                                    <div className="tumb">
                                        <img src={cryptoplatimg1} alt="" />
                                    </div>
                                    <h5 className="box-title">Reliable and Safe</h5>
                                    <p className="text">
                                        We use state of the art technology to keep your funds safe
                                    </p>
                                </div>
                            </div>
                            <div className="col-xl-4 col-lg-6 text-center">
                                <div className="platform-box">
                                    <div className="tumb">
                                        <img src={cryptoplatimg2} alt="" />
                                    </div>
                                    <h5 className="box-title">Variety of Currencies</h5>
                                    <p className="text">
                                        Lend and Borrow BTC, ETH, XRP, LTC, BCH, USDT, TUSD, DAI..
                                    </p>
                                </div>
                            </div>
                            <div className="col-xl-4 col-lg-6 text-center">
                                <div className="platform-box">
                                    <div className="tumb">
                                        <img src={cryptoplatimg3} alt="" />
                                    </div>
                                    <h5 className="box-title">Easy to Use</h5>
                                    <p className="text">Easy registration, fast auto
                                        verification, competitive rates</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="process">
                    <img src={mayaimg} alt="" className="maya" />
                    <img className="plane" src={aeroplaneimg} alt="" />
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-12 text-center">
                                <div className="section-head">
                                    <h4 className="lasthead">How does it work?</h4>
                                    <h2 className="title">It's really easy!</h2>
                                    <p className="text">
                                        It's easier than you think.Follow 3 simple easy steps
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xl-4 col-md-6 text-center">
                                <div className="process-box">
                                    <div className="tumb">
                                        <img src={processimg1} alt="" />
                                    </div>
                                    <p className="text">
                                        SIGN UP
                                        <span className="number">1</span>
                                    </p>
                                </div>
                            </div>
                            <div className="col-xl-4 col-md-6 text-center">
                                <div className="process-box">
                                    <div className="tumb">
                                        <img src={processimg2} alt="" />
                                    </div>
                                    <p className="text">
                                        Deposit Collateral
                                        <span className="number">2</span>
                                    </p>
                                </div>
                            </div>
                            <div className="col-xl-4 col-md-6 text-center">
                                <div className="process-box">
                                    <div className="tumb">
                                        <img src={processimg3} alt="" />
                                    </div>
                                    <p className="text">
                                        Confirm loan terms
                                        <span className="number">3</span>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Cryptocurrency