import React from "react";
import NavPage from "./NavPage";

const PoolPage = () => {
  return (
    <>
      <NavPage />
      <div className='banner' id='home' style={{ padding: '100px' }}>
        <div className='hero-area' id='about'>
          <div className='container'>
            <div className='row justify-content-between'>
              <div className='col-xl-12 col-lg-12'>
                <div className='banner-content'>
                  <p style={{ fontSize: "18px" }}>
                    A staking pool allows multiple stakeholders (or bagholders)
                    to combine their computational resources as a way to
                    increase their chances of being rewarded. In other words,
                    they unite their staking power in the process of verifying
                    and validating new blocks, so they have a higher probability
                    of earning the block rewards. Staking Rewards is on a
                    mission to generate awareness for these exciting new
                    industry. Staking Rewards is a platform of open-concourse,
                    that provides data and guidance to minimize the risk when
                    investing in the countless oppportunities to earn passive
                    income with cryptocurrencies.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default PoolPage;
