import React from "react";
import NavPage from "./NavPage";

const ReferralPage = () => {
  return (
    <>
      <NavPage />
      <div className='banner' id='home' style={{ padding: '100px' }}>
        <div className='hero-area' id='about'>
          <div className='container'>
            <div className='row justify-content-between'>
              <div className='col-xl-12 col-lg-12'>
                <div className='banner-content'>
                  <p>
                    Bintex futures will provide referral rewards for
                    StakingDapp. The Application offers 10% refferal Bonus for
                    referring a friend those rewards automatically sent to your
                    wallet address whenever your friends stake BNTX
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ReferralPage;
