import React, { Component } from 'react'

export class Poolsinfo extends Component {
	constructor(props) {
		super(props);
		this.state = {
			firstPool: [],
			secondPool: [],
			thirdPool: []
		};
	}

	componentDidMount() {
		console.log("PoolData :: ", this.props.poolData);
		this.getPoolDataByLocChoice(this.props.poolData);
	}

	shouldComponentUpdate(nextProps, nextState) {
		// Rendering the component only if 

		if (nextProps.poolData !== this.props.poolData || (nextState.firstPool !== this.state.firstPool || nextState.secondPool !== this.state.secondPool || nextState.thirdPool !== this.state.thirdPool))
			return true;

		return false;
	}

	componentDidUpdate(prevProps, prevState) {
		if (prevProps.poolData !== this.props.poolData) {
			console.log(this.props.poolData);
			this.getPoolDataByLocChoice(this.props.poolData);
		}
	}

	getPoolDataByLocChoice = (poolData) => {
		var firstPool = [], secondPool = [], thirdPool = [];
		firstPool = poolData.filter((pool, index) => pool.lockChoice === 0);
		secondPool = poolData.filter((pool, index) => pool.lockChoice === 1);
		thirdPool = poolData.filter((pool, index) => pool.lockChoice === 2);
		console.log("pool ******** ::: ", firstPool, secondPool, thirdPool);
		this.setState({ firstPool, secondPool, thirdPool });
	}

	showStakeDataTable = (poolData) => {
		const { findDetails, privateInvestor, unStake, claimIntrest, redeem } = this.props;
		var details;
		console.log("showStakeDataTable:: ", poolData);
		return (
			<>
				{poolData.length > 0 ?
					<table className="table text-center table-responsive-xl">
						<thead>
							<th> S.No </th>
							<th> Stake Id </th>
							<th> Redeemable from </th>
						</thead>
						<tbody>
							{poolData && poolData.map((data, index) => {
								details = findDetails(data);
								return [
									<tr key={index} onClick={() => data.expanded = !data.expanded}>
										<td style={{ letterSpacing: '30px' }}> {data.expanded ? '&ndash;' : '+'} {index + 1}
										</td>
										<td> {data.stakeId} </td>
										<td> {data.stakeExpiryTime} </td>
									</tr>,

									// data.expanded,
									details && details.map((detail, index) => (
										<tr key={index + "DetailsIndex"}>
											<td style={{ paddingLeft: '12px' }}> Stake Amount : {detail.stakeDetails.stakeAmount}</td>
											<td> Staking on : {detail.stakeDetails.stakeStartTime} </td>
											<td> Staking Expiry on : {detail.stakeDetails.stakeExpiryTime}</td>
										</tr>
									)),

									details && details.map((detail, index) => (
										<tr key={index + "ButtonsIndex"}>
											{data.redeem < 1 ? <td>
												<a className="nav-link button-1" onClick={() => redeem(detail.whoseData)} role="button" >Redeem</a>
											</td> : null}
											{detail.stakeDetails.unstake === true && privateInvestor === false ?
												<td>
													<a type="button" className="nav-link button-1" onClick={() => unStake(detail.whoseData)} role="button">Unstake</a>
												</td >
												: null}
											<td>
												<a className="nav-link button-1" onClick={() => claimIntrest(detail.whoseData)} style={{ pointerEvents: !data.claimFrom >= 5 ? 'none' : 'auto' }} role="button">Claim Interest</a>
											</td>
										</tr>
									))
								]
							})}
						</tbody>
					</table>
					: null}
			</>
		)
	}

	render() {
		return (
			<>
				<div className="pools-id" style={{ marginTop: '50px' }}>
					<div className="row">
						<div className="col-xl-4">
							<h5>POOLS</h5>
						</div>
						<div className="col-xl-6">
							<h5>TOTAL AMOUNT STAKED:</h5>
						</div>
					</div>
				</div>
				<div className="bg-form" style={{ marginTop: '50px' }}>
					<div className="row">
						<div className="col-xl-4 col-6">
							<h4>POOL-1</h4>
						</div>
						<div className="col-xl-6 col-6">
							<span className="deposit">
								<a className="nav-link button-1" href="#">Deposit</a>
							</span>
						</div>
					</div>
					<hr />
					<div className="row">
						<div className="col-xl-4 col-6">
							<span className="custom">
								<p>Minimum Lock time</p>
								<p>APY</p>
								<p>Total Value Locked</p>
							</span>
						</div>
						<div className="col-xl-6 col-6">
							<span className="deposit-p">
								<p>6 Months</p>
								<p>36%</p>
								<p></p>
							</span>
						</div>
						{this.showStakeDataTable(this.state.firstPool)}
					</div>
				</div>

				<div className="bg-form" style={{ marginTop: '50px' }}>
					<div className="row">
						<div className="col-xl-4 col-6">
							<h4>POOL-2</h4>
						</div>
						<div className="col-xl-6 col-6">
							<span className="deposit">
								<a className="nav-link button-1" href="#">Deposit</a>
							</span>
						</div>
					</div>
					<hr />
					<div className="row">
						<div className="col-xl-4 col-6">
							<span className="custom">
								<p>Minimum Lock time</p>
								<p>APY</p>
								<p>Total Value Locked</p>
							</span>
						</div>
						<div className="col-xl-6 col-6">
							<span className="deposit-p">
								<p >12 Months</p>
								<p >48%</p>
								<p></p>
							</span>
						</div>
						{this.showStakeDataTable(this.state.secondPool)}
					</div>
				</div>


				<div className="bg-form" style={{ marginTop: '50px', marginBottom: '40px' }}>
					<div className="row">
						<div className="col-xl-4 col-6">
							<h4>POOL-3</h4>
						</div>
						<div className="col-xl-6 col-6">
							<span className="deposit">
								<a className="nav-link button-1" href="#">Deposit</a>
							</span>
						</div>
					</div>
					<hr />
					<div className="row">
						<div className="col-xl-4 col-6">
							<span className="custom">
								<p>Minimum Lock time</p>
								<p>APY</p>
								<p>Total Value Locked</p>
							</span>
						</div>
						<div className="col-xl-6 col-6">
							<span className="deposit-p">
								<p>24 Months</p>
								<p>60%</p>
								<p></p>
							</span>
						</div>
						{this.showStakeDataTable(this.state.thirdPool)}
					</div>
				</div>
			</>
		)
	}
}

export default Poolsinfo;