import React, { Component } from 'react'
import videoilstation from "./assets/img/video-ilstation.png";
import videobg from "./assets/img/video-bg.png";
import landingbg from "./assets/img/landing-bg.png";
import checkimg from "./assets/img/check.png";
import laptopgirl from "./assets/img/laptop-girl.png";

export class Lendinginfo extends Component {
	render() {
		return (
			<div className="platfromland platfromland-full" >
				<div className="instruction-video">
					<div className="container">
						<div className="row">
							<div className="col-12">
								<div className="videoo">
									<img src={videoilstation} alt="" className="video-ilstation" />
									<img src={videobg} alt="" className="video-bg" />
								</div>
							</div>
						</div>
					</div>
				</div>
				<div className="lending">
					<div className="bg-img">
						<img src={landingbg} alt="" className="landing-bggg" />
					</div>
					<div className="container">
						<div className="row align-items-center justify-content-between">
							<div className="col-xl-7">
								<div className="section-head">
									<h4 className="lasthead">Coming Soon</h4>
									<h2 className="title">Lending Or Borrowing</h2>
									<p className="text">
										Start Lending and Borrowing with Bintex Futures
									</p>
									<ul className="list">
										<li className="list-item">
											<span className="check">
												<img src={checkimg} alt="" />
											</span>
											<p className="text">APY on your deposits</p>
										</li>
										<li className="list-item">
											<span className="check">
												<img src={checkimg} alt="" />
											</span>
											<p className="text">Low minimum deposit</p>
										</li>
										<li className="list-item">
											<span className="check">
												<img src={checkimg} alt="" />
											</span>
											<p className="text">Loan interest rates - as low as Possible</p>
										</li>
										<li className="list-item">
											<span className="check">
												<img src={checkimg} alt="" />
											</span>
											<p className="text">Bank Grade Security</p>
										</li>
									</ul>
									<a href="#" className="button button-1">Get Started Today</a>
								</div>
							</div>
							<div className="col-xl-5 d-none d-xl-block">
								<div className="pic">
									<img src={laptopgirl} alt="" className="laptop-girl" />
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

export default Lendinginfo;