import React, { useState, useEffect } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";

import BintecFutures from "./assets/img/bintex.png";
import "./assets/css/bootstrap.min.css";
import "./assets/demo/demo.css";

const NavPage = ({ onConnectClick = () => { }, onDisconnectClick = () => { }, walletDetails = {} }) => {
  const [navState, setNavState] = useState({
    isCollapsed: true,
  });

  const history = useHistory();
  const location = useLocation();

  const goToHome = () => {
    history.push(`/`);
  };

  useEffect(() => {
    console.log(location.pathname);
  });

  return (
    <div>
      <div className='mein-menu'>
        <nav
          className='navbar navbar-expand-lg navbar-dark'
          color-on-scroll='100'
          id='navbar-top'
        >
          <div className='container'>
            <a className='navbar-brand' href='#'>
              <img
                src={BintecFutures}
                className='rounded mx-auto pl-1 text-center'
                style={{ width: "200px", cursor: "pointer" }}
                alt='logo'
                onClick={() => goToHome()}
              />
            </a>
            {/*<div className="navbar-translate">
                            <button aria-controls="navigation-index" aria-label="Toggle navigation" className="navbar-toggler" aria-expanded={!navState.isCollapsed} onClick={() => setNavState({ ...navState, isCollapsed: !navState.isCollapsed })} id="navigation" type="button">
                                <span className="navbar-toggler-bar bar1"> </span>
                                <span className="navbar-toggler-bar bar2"> </span>
                                <span className="navbar-toggler-bar bar3"> </span>
                            </button>
    </div>*/}
            <button
              className='navbar-toggler'
              type='button'
              data-bs-toggle='collapse'
              data-bs-target='#navbarNavDropdown'
              aria-controls='navbarNavDropdown'
              aria-expanded='false'
              aria-label='Toggle navigation'
            >
              <span className='navbar-toggler-icon'></span>
            </button>
            <div className='collapse navbar-collapse' id='navbarNavDropdown'>
              <div className='navbar-collapse-header'>
                <div className='row'>
                  <div className='col-6 collapse-brand'></div>
                  <div className='col-6 collapse-close text-right'>
                    <button
                      aria-controls='navigation-index'
                      aria-label='Toggle navigation'
                      className='navbar-toggler'
                      aria-expanded={!navState.isCollapsed}
                      onClick={() =>
                        setNavState({
                          ...navState,
                          isCollapsed: !navState.isCollapsed,
                        })
                      }
                      id='navigation'
                      type='button'
                    >
                      <i className='tim-icons icon-simple-remove'> </i>
                    </button>
                  </div>
                </div>
              </div>
              <ul className='navbar-nav'>
                <li className='nav-item'>
                  <Link to='/' className='nav-link'>
                    HOME
                  </Link>
                </li>
                <li className='nav-item'>
                  <Link to='/about' className='nav-link'>
                    ABOUT
                  </Link>
                </li>
                <li className='nav-item'>
                  <Link to='/staking' className='nav-link'>
                    STAKING
                  </Link>
                </li>
                <li className='nav-item'>
                  <Link to='/referral' className='nav-link'>
                    REFERRAL
                  </Link>
                </li>
                <li className='nav-item'>
                  <Link to='/contact' className='nav-link'>
                    CONTACT
                  </Link>
                </li>

                {location.pathname === "/admin" || location.pathname === "/stakingDapp" ? (
                  <li className='nav-item'>
                    {walletDetails && walletDetails.status !== "connected" ? (
                      <a onClick={onConnectClick} className='nav-link button-1' role="button">
                        CONNECT
                      </a>
                    ) : null}
                    {walletDetails && walletDetails.status === "connected" ? (
                      <a className='nav-link button-1' onClick={onDisconnectClick} role='button'
                      >
                        DISCONNECT
                      </a>
                    ) : null}
                  </li>
                ) : null}
              </ul>
            </div>
          </div>
        </nav>
      </div>
    </div>
  );
};

export default NavPage;
