import React, { Component } from "react";

export class Stackinginfo extends Component {
  render() {
    const { userState, walletDetails } = this.props;
    let userDataLength = userState.userData.length || 0;
    return (
      <>
        <div className='platfromland p-b-80' id='staking'>
          <div className='platform' id='platform'>
            <div className='container'>
              <div className='row'>
                <div className='col-12 text-center'>
                  <div className='section-head'>
                    {/* <h4 className="lasthead"> Staking Rewards</h4> */}
                    <h2 className='title'>Staking Rewards</h2>
                  </div>
                </div>
                <div className='col-xl-4 col-lg-6 text-center'>
                  <div className='platform-box'>
                    <div className='tumb'></div>
                    <h1 className='box-title'>
                      {userState.userTotalStakeBalance}
                    </h1>
                    <p className='text'>Token Stake Balance</p>
                    <a
                      className='nav-link button-1'
                      onClick={() => this.props.onLoadClick()}
                      style={{
                        marginTop: "50px",
                        marginLeft: "62px",
                        pointerEvents:
                          this.props.userState.walletAddress === null
                            ? "none"
                            : "auto",
                      }}
                    >
                      Load
                    </a>
                  </div>
                </div>
                <div className='col-xl-4 col-lg-6 text-center'>
                  <div className='platform-box'>
                    <div className='tumb'></div>
                    <h1 className='box-title'>{userState.tokenBalance}</h1>
                    <p className='text'>Token Balance</p>
                    {this.props.walletDetails.status !== "connected" ? (
                      <a
                        className='nav-link button-1'
                        onClick={() => this.props.onConnectClick()}
                        style={{ marginTop: "50px", marginLeft: "62px" }}
                      >
                        Connect
                      </a>
                    ) : null}
                    {this.props.walletDetails.status === "connected" ? (
                      <a
                        className='nav-link button-1'
                        style={{ marginTop: "50px", marginLeft: "62px" }}
                        onClick={() => this.props.onDisconnectClick()}
                        role='button'
                      >
                        Disconnect
                      </a>
                    ) : null}
                  </div>
                </div>
                <div className='col-xl-4 col-lg-6 text-center'>
                  <div className='platform-box'>
                    <div className='tumb'></div>
                    {userState.showResponse === false ? (
                      <h1 className='box-title'> NA</h1>
                    ) : null}
                    {userState.showResponse === true ? (
                      <h1 className='box-title'>{userState.referalBonus}</h1>
                    ) : null}
                    <p className='text'>Referral Bonus</p>
                    {userState.referalBonus > 0 ? (
                      <a
                        type='button'
                        className='nav-link button-1'
                        style={{
                          pointerEvents: userState.isLoadingClaimButton
                            ? "none"
                            : "auto",
                          marginTop: "50px",
                          marginLeft: "62px",
                          padding: "0px",
                        }}
                        onClick={() => this.props.claimReferral()}
                      >
                        {" "}
                        Claim{" "}
                      </a>
                    ) : null}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='bg-form'>
            <form>
              <h4>Wallet Address</h4>
              <input
                type='text'
                name='text'
                placeholder='Enter Wallet Address'
                className='input-w'
                id='walletAddress'
                value={this.props.userState.walletAddress}
              />
              <input
                type='button'
                onClick={() => this.props.onLoadClick()}
                name='submit'
                value='Load'
                className='nav-link button-1'
                style={{ float: "right", border: "navajowhite" }}
                id='button-l'
              />
            </form>
          </div>
          <div className='bg-form' style={{ marginTop: "50px" }}>
            <form>
              <h4>
                {userDataLength > 0 ? `Total ${userDataLength}` : `NO`} STAKES
                AVAILABLE FOR THIS WALLET ADDRESS
              </h4>
              <input
                type='button'
                name='submit'
                value='Create Stake'
                className='nav-link button-1'
                style={{
                  float: "right",
                  border: "navajowhite",
                  width: "140px",
                }}
                id='button-l'
                onClick={() => this.props.onCreateStakeClick()}
              />
            </form>
          </div>
          <div className='bg-form' style={{ marginTop: "50px" }}>
            <h4>Bintexfutures Staking?</h4>
            <p>
              Staking essentially means depositing BNTX tokens within the
              protocol Safety Module. The purpose of staking is stakers will
              receive BNTX as rewards. The Staking rewards are
            </p>
            <p>36% APY for 6 months Locking</p>
            <p>48% APY for 12 months Locking</p>
            <p>60% APY for 24 months Locking</p>
            <p>
              And rewards will be distributed to the stakers on monthly basis.
            </p>

            {walletDetails.status !== "connected" ? (
              <a
                className='nav-link button-1'
                style={{ lineHeight: "32px" }}
                onClick={() => this.props.onConnectClick()}
              >
                CONNECT
              </a>
            ) : null}
            {walletDetails.status === "connected" ? (
              <a
                className='nav-link button-1'
                onClick={() => this.props.onDisconnectClick()}
              >
                Disconnect
              </a>
            ) : null}
          </div>
        </div>
      </>
    );
  }
}

export default Stackinginfo;
