import React, { Component } from 'react'
import Particles from 'react-particles-js';

export class Particleinfo extends Component {
  render() {
    return (
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: '100%', height: '100%' //, zIndex: '999999'
        }}>
        <Particles
          params={{
            particles: {
              number: {
                value: 40,
              },
              color: {
                value: ['#fff']
              },
              line_linked: {
                color: '#999999',
                opacity: 1
              },

              shape: {
                type: "circle",
                stroke: {
                  width: 4,
                  color: "#999999"
                },
                polygon: {
                  nb_sides: 4
                },
                image: {
                  src: "",
                  width: 100,
                  height: 100
                }
              },
              move: {
                enable: true,
                speed: 2,
                direction: "none",
                random: false,
                straight: false,
                out_mode: "out",
                bounce: false,
                attract: {
                  enable: false,
                  rotateX: 600,
                  rotateY: 1200
                }
              }
            },
            interactivity: {
              detect_on: "window",
              events: {
                onhover: {
                  enable: true,
                  mode: "repulse"
                },
                onclick: {
                  enable: false,
                  mode: "bubble"
                },
                resize: true
              },
              modes: {
                grab: {
                  distance: 400,
                  line_linked: {
                    opacity: 1
                  }
                },
                bubble: {
                  distance: 400,
                  size: 40,
                  duration: 2,
                  opacity: 8,
                  speed: 3
                },
                repulse: {
                  distance: 100,
                  duration: 0.4
                },
                push: {
                  particles_nb: 4
                },
                remove: {
                  particles_nb: 2
                }
              }
            },
            retina_detect: true
          }}
        />


      </div>
    )
  }
}

export default Particleinfo