import React from "react";
import NavPage from "./NavPage";

const AboutPage = () => {
  return (
    <>
      <NavPage />
      <div className="banner" id="home" style={{ padding: '100px' }}>
        <div className="hero-area" id="about">
          <div className="container">
            <div className="row justify-content-between">
              <div className="col-xl-12 col-lg-12">
                <div className="banner-content">
                  <p>
                    Bintex Futures is a world-class blockchain company comprised
                    of BintexPAY and various other cryptocurrency-related
                    functions.
                  </p>
                  <br />
                  <p>
                    Bintex Futures is a proposed decentralised crypto currency
                    exchange platform which will trade Bintex Pay and other
                    crypto currencies.
                  </p>
                  <br />
                  <p>
                    Bintex futures Staking dapp is an open-source and
                    non-custodial protocol to earn interest on deposits and
                    lending assets.
                  </p>
                  <br />
                  <p>
                    “Bintex Token” which is going to be mandatory to trade on
                    Bintex Futures Exchange for better liquidation and also
                    there’re various real time applications for this token for
                    providing best liquidation and use case scenarios in real
                    time.
                  </p>
                  <br />
                  <p>
                    Another product in pipeline is going to be “Bintex Pay
                    Wallet” – which is a private wallet for each customer to
                    store various crypto currencies along with the native
                    “Bintex Token”.
                  </p>
                  <br />
                  <p>
                    “Bintex Pay Card” is another extension project associated
                    with “Bintex Wallet” where this card is similar to our any
                    debit cards, where direct liquidation of crypto assets can
                    be facilitated for real time shopping, purchases etc.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default AboutPage;
